export class User {
  constructor(information) {
    // Le constructeur de la classe User est appel√© lorsqu'une nouvelle instance est cr√©√©e.
    // Il prend les informations d'un utilisateur en tant que param√®tre.

    // Stocke les informations pass√©es en param√®tre dans la propri√©t√© "information".
    this.information = information;

    // Initialise l'√©tat de pr√©sence √† "false".
    //je sais pas pourquoi √ßa fonctionne avec true
    this.present = true;

    // Appelle la m√©thode createHtml pour g√©n√©rer l'√©l√©ment utilisateur.
    this.element = this.createHtml();

    // D√©finir l'attribut data-present en fonction de la valeur initiale de this.present.
    this.element.dataset.present = this.present;

    /*Comme mentionn√© ci-dessus, il va falloir g√©rer un click sur un √©l√©ment utilisateur de telle sorte √† ce que son √©tat de pr√©sence 
s‚Äôinverse lorsque l‚Äô√©v√©nement √† lieu. Le plus simple est de stocker l‚Äô√©l√©ment lors de la cr√©ation de l‚Äôobjet (constructor() ) 
et de lui ajouter un eventListener au m√™me endroit. */
    //Gestion du click sur un √©l√©ment utilisateur
    // Ajoute un √©couteur d'√©v√©nement "click" sur l'√©l√©ment pour g√©rer le changement d'√©tat de pr√©sence.

    // l'utilisation de bind(this) garantit que this dans la m√©thode togglePresence fait r√©f√©rence √† l'instance correcte de la classe User, ce qui vous permet d'acc√©der aux propri√©t√©s et m√©thodes de cette instance comme pr√©vu.
    //C'est une technique courante pour g√©rer les √©couteurs d'√©v√©nements tout en maintenant le contexte appropri√© pour les m√©thodes de classe.
    this.element.addEventListener("click", this.togglePresence.bind(this));
  }

  //m√©thode pour g√©n√©rer un √©l√©ment HTML
  createHtml() {
    const containerElement = document.createElement("div");
    containerElement.classList.add("user");

    const childHtml = `
      <img src="${this.information.picture.large}">
      <div class="user--info">
          <h1>${this.information.name.title} ${this.information.name.first} ${this.information.name.last}</h1>
          <p>${this.information.dob.age} years old</p>
          <p>${this.information.location.city}</p>
      </div>
      <a href="mailto:${this.information.email}">
          <span class='mail'>‚úâÔ∏è</span>
      </a>`;

    containerElement.insertAdjacentHTML("afterbegin", childHtml);

    return containerElement; // Retourne l'√©l√©ment g√©n√©r√©.
  }

  /*
### M√©thode d‚Äôaffichage des √©l√©ments utilisateurs

√Ä ce stade-ci vous devriez pouvoir stocker les √©l√©ments correspondant √† un profil utilisateur dans la propri√©t√© pr√©vue √† cet effet. Il faut cependant encore afficher cet √©l√©ment sur la page elle-m√™me. Cr√©ez donc une m√©thode permettant de faire cela. Un bon nom  pour cette m√©thode serait `render()`, mais vous pouvez √©videmment l‚Äôappeler comme vous voulez. 
Au final, ces √©l√©ments devront imp√©rativement √™tre affich√©s comme enfants de l‚Äô√©l√©ment `<main>` cod√© directement dans `index.html`
  */
  //m√©thode d'affichage visuel

  render() {
    const main = document.querySelector("main");
    main.insertAdjacentElement("afterbegin", this.element);
  }

  //m√©thode pour mettre √† jour le compteur
  updateCounter() {
    // S√©lectionnez l'√©l√©ment de compteur dans le DOM
    const counterElement = document.querySelector(".counter");

    // Comptez le nombre d'utilisateurs pr√©sents en parcourant les √©l√©ments d'utilisateurs
    const presentUsers = document.querySelectorAll(
      ".user[data-present='true']"
    ).length;

    // Mettez √† jour le texte du compteur avec le nombre d'utilisateurs pr√©sents
    counterElement.textContent = `${presentUsers}/20 people are here`;
  }

  /* Dans css : 
  main .user[data-present="true"] {
    background-color: #06d6a0;
    color: #ffffff;
  }
  
  main .user[data-present="true"] p {
    color: #ffffff;
  }
  */

  //m√©thode toggle pour dire si l'utilisateur est pr√©sent ou non, en se basant sur le css
  togglePresence() {
    //this.present = !this.present;
    //inverse l'√©tat de pr√©sence
    this.present ? (this.present = false) : (this.present = true);
    //// Met √† jour l'attribut "data-present" pour refl√©ter le nouvel √©tat de pr√©sence
    this.element.dataset.present = this.present;
    this.element.dataset.present = this.present;
    console.log(this.element.dataset.present);

    //pour le counter
    this.updateCounter();
  }
}
