import { User } from "./User.js";
let cleanArray = []; // D√©finir cleanArray en tant que variable globale

async function getusers() {
  const reponse = await fetch("https://randomuser.me/api/?results=20");
  const userArray = await reponse.json();
  //pour voir les donn√©es
  console.log(userArray);
  //on veut cr√©er un tableau ne contenant que les informations essentielles
  //on voit dans le console.log que c'est dans results
  //const cleanArray = [];
  userArray.results.forEach((e) => {
    cleanArray.push({
      name: {
        title: e.name.title,
        first: e.name.first,
        last: e.name.last,
      },
      location: {
        city: e.location.city,
      },
      dob: {
        age: e.dob.age,
      },
      email: e.email,
      picture: {
        large: e.picture.large,
      },
    });
  });
  // trie par ordre alphab√©tique en arrivant sur la page
  ordreAlphabetique(cleanArray);
  sortByNameButton = document.getElementById("sort--name");
  sortByAgeButton = document.getElementById("sort--age");

  // Ajoutez l'√©couteur d'√©v√©nement pour le bouton "Sort by name"
  sortByNameButton.addEventListener("click", () => {
    sortByNameButton.classList.add("selected");
    sortByAgeButton.classList.remove("selected");
    ordreAlphabetique(cleanArray);
    updateMainContent(); // Mettez √† jour le contenu de la section <main> avec le nouveau tri
  });

  // Ajoutez l'√©couteur d'√©v√©nement pour le bouton "Sort by age"
  sortByAgeButton.addEventListener("click", () => {
    sortByAgeButton.classList.add("selected");
    sortByNameButton.classList.remove("selected");
    ordreAge(cleanArray); // Appel de la fonction pour trier par ordre d'√¢ge
    updateMainContent(); // Mettez √† jour le contenu de la section <main> avec le nouveau tri
  });
  cleanArray.forEach((e) => {
    const user = new User(e);
    //pour rappel, render cr√©e l'√©l√©ment html
    user.render();
    user.togglePresence();
  });

  console.log(cleanArray);

  /*## BONUS - Changer l‚Äôordre des profils 

Utilisez les √©l√©ments cod√©s directement dans le fichier `index.html` pour changer l‚Äôordre d‚Äôaffichage de √©l√©ments: 

```html
<button id="sort--name" class="selected">Sort by name</button>
<button id="sort--age">Sort by age</button>
```

- Le bouton `sort‚Äî-name` doit afficher les √©l√©ments utilisateur par ordre A ‚Üí Z du nom de famille
- Le bouton `sort‚Äîage` doit afficher les √©l√©ments utilisateur par ordre du plus jeune au plus √¢g√©.

Tout le container `<main>` devra √™tre mis √† jour lors d‚Äôun click!

  */
  //m√©thode qui lorsqu'on clique sur le bouton sortbyname, √ßa trie par ordre alphab√©tique, et si on clique sur le bouton sortbyage, √ßa trie par ordre d'√¢ge
}

getusers();

//function pour l'ordre alphab√©tique (appel√©e plus haut)
function ordreAlphabetique(array) {
  array.sort((a, b) => {
    if (a.name.last > b.name.last) return -1;
    else return 1;
  });

  // return array;
}
function ordreAge(array) {
  array.sort((a, b) => {
    if (a.dob.age > b.dob.age) return -1;
    else return 1;
  });
}
// Fonction pour mettre √† jour le contenu de la section <main>
function updateMainContent() {
  const main = document.querySelector("main");
  // En effa√ßant le contenu actuel avant d'ajouter les nouveaux √©l√©ments, vous vous assurez que seuls les profils tri√©s sont affich√©s, sans accumuler les anciens profils.
  main.innerHTML = ""; // Efface le contenu actuel

  cleanArray.forEach((e) => {
    const user = new User(e);

    user.render();
    user.togglePresence();
  });
}
